import React, { useState } from "react";
import { HashLink } from "react-router-hash-link";
import "./Circle.scss";
const Circle = ({ transitionView, links }) => {
  const steps = ["Licenses", "Upgrades", "Bundles", "Extras"];
  /*   const [show, showSet] = useState(false); */
  /*   const onMouseEnter = () => showSet(true);
  const onMouseLeave = () => showSet(false); */
  const renderWithLink = (index, hash) => {
    console.log(hash);
    return (
      <>
        <div
          className={`round-box position-relative mx-2 ${
            index <= transitionView - 1 ? "circle-selected" : ""
          }`}
        >
          <p className="step position-absolute d-none d-md-block">
            {steps[index]}
          </p>
        </div>
        {/*         <i className="fas fa-pen position-relative mx-2 pen">
          <p className="step position-absolute d-none d-md-block">
            {steps[index]}
          </p>
        </i> */}
      </>
    );
  };
  return (
    <div className="d-flex circle-container position-absolute ">
      {[...Array(4)].map((item, index) => {
        return (
          <div key={index} className="circle">
            <div className="steps-box ">
              <div className="circle-box d-flex align-items-center ">
                {index <= transitionView - 2 ? (
                  <HashLink
                    /*
                    onMouseEnter={onMouseEnter}
                    onMouseLeave={onMouseLeave} 
                    */
                    to={`${links[index]}#transition`}
                    className="hash-circle"
                    scroll={(el) =>
                      el.scrollIntoView({ behavior: "smooth", block: "start" })
                    }
                  >
                    {renderWithLink(index, true)}
                  </HashLink>
                ) : (
                  renderWithLink(index)
                )}
                {index <= 2 && (
                  <div
                    className={`circle-line  d-none d-md-block ${
                      index <= transitionView - 2 ? "circle-selected " : ""
                    }`}
                  ></div>
                )}
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Circle;
